<template>
<div>
    <u-button v-repeat-click="onClick">Click Me!</u-button> {{ count }}
</div>
</template>
<script>
export default {
    data() {
        return {
            count: 0,
        };
    },
    methods: {
        onClick() {
            this.count++;
        },
    },
};
</script>



